*, *::before, *::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    width: 100vw;
    min-height: 100vh;
}

h2 {
    font-style: italic;
    color: plum;
}

.country {
    display: inline-block
}

.emoji {
    cursor: pointer;
    margin-left: 0.25rem;
    margin-right: 0.25rem;
}